<template>
    <div>
      <el-container>
        <el-header style="height: 5%"></el-header>
        <el-main style="display: flex; flex-direction: column">
          <div>
            <div
              style="
                display: flex;
                width: auto;
                flex-direction: column;
                align-items: center;
                justify-content: center;
              "
            >
              <el-input
                placeholder="请输入..."
                v-model="input3"
                style="width: auto"
              >
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="search"
                ></el-button>
              </el-input>
            </div>
          </div>
          <div style="height: 45px"></div>
          <div style="display: flex">
            <div style="width: 150px"></div>
            <div
              style="
                width: 250px;
                height: 750px;
                background: #ffffff;
                border-radius: 6px;
              "
            >
            <el-row
                class="tac"
                style="
                  width: auto;
                  height: 750px;
                  background: #ffffff;
                  border-radius: 6px;
                "
              >
                <el-col :span="24">
                  <el-menu default-active="8" style="line-height: 25px">
                    <div style="height: 10px"></div>
  
                    <el-menu-item index="1">
                      <i class="el-icon-picture-outline-round"></i>
                      <span slot="title" @click="hand1">道具专区</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                      <i class="el-icon-mouse"></i>
                      <span slot="title" @click="hand2">宠物专区</span>
                    </el-menu-item>
  
                    <el-menu-item index="3">
                      <i class="el-icon-s-custom"></i>
                      <span slot="title" @click="hand3">账号专区</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                      <i class="el-icon-s-check"></i>
                      <span slot="title" @click="hand4">代练专区</span>
                    </el-menu-item>
                    <el-menu-item index="5">
                      <i class="el-icon-s-goods"></i>
                      <span slot="title" @click="hand5">求购专区</span>
                    </el-menu-item>
                    <el-menu-item index="6">
                      <i class="el-icon-setting"></i>
                      <span slot="title" @click="hand6">攻略专区</span>
                    </el-menu-item>
  
                    <el-menu-item index="7">
                      <i class="el-icon-setting"></i>
                      <span slot="title" @click="hand7">工具专区</span>
                    </el-menu-item>
                    <el-menu-item index="8">
                      <i class="el-icon-warning-outline"></i>
                      <span slot="title" @click="hand8">骗子专区</span>
                    </el-menu-item>
                  </el-menu>
  
                  <div style="display: flex; justify-content: center;">
    <div
      style="
        position: relative;
        width: 240px;
        height: 165px;
        overflow-y: hidden;
        overflow-y: hidden;
                        margin-top: -35px;
                        z-index: 2;
      "
    >
      <img
        src="@/assets/tian/11111.png"
        style="
          height: 100%;
          width: auto; /* 将宽度设置为自动，以保持原始比例 */
          position: relative;
          z-index: 2;
        "
      />
    </div>
  </div>
  
                </el-col>
              </el-row>
            </div>
            <div style="width: 60px"></div>
            <div
              style="
                top: 5%;
                right: 150px;
                height: auto;
                flex: 1;
                background: #ffffff;
                border-radius: 6px;
              "
            >
              
             
  
  
            <div class="table-container">
              <div style="height: 60px;"></div>
        <h1> 骗子专区</h1>
        <el-row class="opt-class">
          <el-col :span="24">
            <el-button type="primary" plain icon="el-icon-edit" @click="toEditor">发布</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="deleteBatch" plain size="small">批量删除</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
                ref="articleTable"
                :data="articleTable"
                stripe
                border
                style="width: 100%">
              <el-table-column
                  type="selection"
                  width="55">
              </el-table-column>
              <el-table-column
                  prop="title"
                  label="标题"
                  >
              </el-table-column>
              <el-table-column
                  prop="createTime"
                  label="创建时间">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
                </template>
              </el-table-column>
              <el-table-column
                  label="操作"
                  align="center"
              >
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="查看详情" placement="top">
                    <el-button type="success" size="small" icon="el-icon-view" @click="preViewArticle(scope.row)"
                               circle></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
            <!--分页组件-->
            <handel-page ref="handlePage" @queryInfo="queryArticleInfo"></handel-page>
          </el-col>
        </el-row>
      </div>
  
  
  
  
  
            </div>
  
  
            <div style="width: 110px"></div>
          </div>
        </el-main>
      </el-container>
    </div>
  </template>
      <script>
 
  
  import HandelPage from "@/components/Page/HandelPage";
    import {queryArticle,deleteArticle} from '@/api/document/pianzi'
  
  export default {
      name: "HomeIndex",
      components: {
        HandelPage
      },
    data() {
      
      return {
          queryForm: {},
          loading: false,
          articleTable: [],
        equipmentId: "",
        carts: [],
        chaxun: {},
        ad: {},
        prev: 0,
        next: 0,
        id: "",
        input3: "",
        input: "",
        activeName: "first",
        number1: null,
        number2: null,
        num1: null,
        num2: null,
        ishot: "",
        isPopupVisible: false,
        chaui: false,
        addui: false,
        imageUrl: "",
        popupPhotoUrl: "", // 添加popupPhotoUrl属性
        currentPage: 1, //初始页
        pagesize: 12, //每页多少数据
        total: "",
      };
    },
    methods: {
    
      hand1() {
        this.$router.push("/home");
      },
      hand2() {
        this.$router.push("/pet01guan");
      },
      hand3() {
        this.$router.push("/accountguan");
      },
      hand4() {
        this.$router.push("/practiseguan");
      },
      hand5() {
        this.$router.push("/qiuguan");
      },
      hand6() {
        this.$router.push("/lueguan");
      },
      hand7() {
        this.$router.push("/toolguan");
      },
      hand8() {
        this.$router.push("/pianziguan");
      },
      hand11() {
        this.$router.push("/home");
      },
      hand12() {
        this.$router.push("/proop02guan");
      },
      hand13() {
        this.$router.push("/proop03guan");
      },
      showPopup(path) {
        // 点击图片时触发弹窗效果
        this.popupPhotoUrl = path; // 设置弹窗图片路径
        this.isPopupVisible = true; // 显示弹窗
      },
      closePopup() {
        // 关闭弹窗
        this.isPopupVisible = false;
      },
      queryArticleInfo() {
          this.loading = true
          this.queryForm.current = this.$refs.handlePage.current;
          this.queryForm.size = this.$refs.handlePage.size;
          queryArticle(this.queryForm).then((res) => {
            this.loading = false
            this.articleTable = res.data.records
            this.$refs.handlePage.total = res.data.total
          })
        },
        /**
         * 批量删除
         */
        deleteBatch() {
          let selectData = this.$refs.articleTable.selection;
          if (selectData.length > 0) {
            this.$confirm('是否删除', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              let ids = [];
              selectData.forEach(item => {
                ids.push(item.id);
              })
              deleteArticle(ids).then((res) => {
                if (res.data === true) {
                  this.queryArticleInfo();
                  this.$message({
                    type: 'success',
                    message: res.msg
                  });
                } else {
                  this.$message({
                    type: 'error',
                    message: res.msg
                  });
                }
              });
            });
          } else {
            this.$message({
              type: 'warning',
              message: '请至少选择一行数据'
            });
          }
        },
        /**
         * 跳转到编辑页面
         */
        toEditor() {
          const routeUrl = this.$router.resolve({
            path: "/editor002"
          });
          window.open(routeUrl.href, "_blank");
        },
        /**
         * 预览
         */
        preViewArticle(row){
          this.$router.push({name:'Detail',query: {id:row.id}})
        }
      
    },
    mounted() {
      // this.fetchData();
      this.queryArticleInfo()
    },
  };
  import "@/assets/css/head.css";
  import "@/assets/css/imagebag.css";
  </script>
      <style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
    
  
    .opt-class {
      text-align: left;
      margin-bottom: 10px;
    }
    
    .table-container{
      width: 1000px;
      margin: 0 auto;
    
    }
  </style>